      desbloqueo:
        "<p>Preguntar cuantas veces realizar la petición.</p><p>Invoco al Comité de Registros Akáshicos, Sanat Kumara, Gautama Buda, Maitreya e Hilarión, para que en esta línea temporal de vida eliminen, disipen y trasmuten todos los bloqueos vinculados a <b>______ (nombre del bloqueo)</b> su causa y efecto, récord y memoria, acción y reacción.</p><p><b>Gracias, gracias, gracias.</b></p>",
