      desbloqueo:
        "<p>Elimina de mi sistema energético la proyección energética que está afectándome. Bórrala de mi registro desde la vida en la que se originó, dejando intactas todas las experiencias y lecciones aprendidas, libres de cualquier trauma o restricción. Elimina toda la energía discordante asociada. </p><p><b>Disuelve este bloqueo en todos los planos, dimensiones y líneas temporales en que se encuentre manifestado; desde el punto y momento espacio temporal en el cual se originó hasta este momento.</b></p><p><b>Gracias, gracias, gracias.</b></p>",
