      preguntas:
        "<p><b>A.</b> ¿Está presente desde nacimiento?</p><p><b>B.</b></p><ul class='browser-default'><li>¿Es una costra puesta para protegernos?</li><li>¿Para escondernos?</li><li>¿Para huir del mundo?</li></ul><p><b>C.</b></p><ul class='browser-default'><li>¿Es general o está localizada en una parte de nuestro sistema energético concreta?</li><li>¿Dónde? (tabla Sist. Energético, si es en la matriz etérea, además tabla Sist. Físico ).</li></ul><p><b>D.</b> ¿Tiene alguna lección de vida asociada? (tabla LV)</p><p><b>E.</b> ¿Hubo algún detonante o situación concreta que la provocó? (tabla de detonantes)</p>",
      desbloqueo:
        "<p>Disipa la costra que he usado con el propósito de protegerme/esconderme (especificar). Disipa esta energía con amor de vuelta a la fuente divina, de forma que ningún ser pueda usar esta costra de nuevo.</p><p><b>Disuelve este bloqueo en todos los planos, dimensiones y líneas temporales en que se encuentre manifestado; desde el punto y momento espacio temporal en el cual se originó hasta este momento.</b></p><p><b>Gracias, gracias, gracias.</b></p>",
