        "<p>ENTIDADES NEGATIVAS<br>Formas energéticas propias que han tomado “autoconciencia”</p><p>Las llamamos también entidades interdimensionales por ser energías conscientes que se convierten en seres capaces de moverse por diferentes planos, especialmente los planos etéricos y astrales, desde donde se anclan a nuestro cuerpo etérico o emocional para nutrirse de nuestra energía.</p><p>Medusas astrales y entidades similares</p><p>Este tipo de entidades pueden ser también autogeneradas, o bien proceder de los planos etéricos o astrales, creadas por las energías del inconsciente colectivo de la humanidad, y las bolsas de emociones negativas existentes en esos planos.</p><p>Se engancha en diferentes chakras, pudiendo bloquear varios de ellos a la vez, tanto por la parte delantera como trasera de los mismos.</p><p>Si está enganchada en el segundo chakra, suele ser debido a que existen energías relacionadas con abusos sexuales que la han atraído, en el tercer chakra, es debido a energías relacionadas con miedos, cólera, odios, etc. En el cuarto chakra, se enganchan debido a miedos y angustia, y en el resto de chakras por bloqueos importantes asociados a cada una de las funciones energéticas de esos vórtices. Pueden medir de 120 a 200 cm.</p>",
