      significado:
        "<p>ENTIDADES NEGATIVAS<br>Formas energéticas propias que han tomado “autoconciencia”</p><p>Las llamamos también entidades interdimensionales por ser energías conscientes que se convierten en seres capaces de moverse por diferentes planos, especialmente los planos etéricos y astrales, desde donde se anclan a nuestro cuerpo etérico o emocional para nutrirse de nuestra energía.</p><p>Flotadores o erizos de mar astrales</p><p>Se forma como si fuera un flotador etérico alrededor de la persona, generado por bloqueos internos, a veces entre el espíritu, alma y el cuerpo físico, almacenando la energía de alguna situación conflictiva alrededor de la persona a la altura del chakra que la debería haber procesado.</p><p>Cuando aparece alrededor del tercer chakra, refleja un conflicto entre lo que uno desea y lo que uno tiene que aceptar (conflicto alma-cuerpo).</p><p>Cuando aparece alrededor del quinto chakra, refleja un conflicto entre lo que uno quisiera hacer o ser y lo que uno debe hacer o ser (conflicto mente-corazón). Puede tener de 60 a 80 cm de diámetro.</p>",
