        "<p><b>A.</b><ul class='browser-default'><li>¿Está anclado a uno o varios chakras?</li><li>¿A cuáles? (determinar los chakras que están siendo usados como puntos de anclaje, normalmente uno de ellos suele ser el tercer chakra, y otro más)</li></ul></p><p><b>B.</b> ¿Qué tipo de entidad es? (tipo reptoide, tipo mantis, tipo insectoide, otro tipo diferente)</p><p><b>C.</b> ¿Qué emoción principal está intentando generar en la persona? (tabla FPN, puede haber varias respuestas)</p><p><b>D.</b><ul class='browser-default'><li>¿Hay algún pacto o acuerdo con esta entidad?</li><li>Si lo hay, ¿proviene de esta vida o de otras? </li><li>¿Hay que sanar primero el pacto? (si es así, hay que hacerlo).</li></ul></p><p><b>E.</b> ¿Ha habido algún detonante para la aparición de esta entidad? (tabla de detonantes)</p><p><b>F.</b> ¿Hay alguna lección de vida asociada? (tabla LV)</p>",
