        "<p><b>Oración de Sacado</b></p><p><b>En el Nombre de Nuestro Señor Maitreya.<br>Con la Asistencia de nuestra Energía Protectora.<br>Con la asistencia y ayuda espiritual del arcángel Miguel, Gabriel, Rafael, Uriel, Aniel y Samuel.<br>Demando, comando y ordeno que la entidad Negativa (bajo o superior astral o poltergeist) no tiene ningún poder sobre mí y sobre ninguna criatura de esta tierra.<br>Pido a los arcángeles, ángeles, seres de luz y seres invocados, que eliminen, disipen y transmuten esta(s) entidad(es) su causa y efecto, record y memoria y acción y reacción, instalando el plan divino en esta tierra y en mi, borrando cualquier atadura e implante que me una a esta(s) entidad(es) y sacándome de sus registros cualquier información que me una a esta entidad u otras entidades de manera directa o indirecta.<br><br>Gracias Gracias Gracias.</b></p><p>Elimina, disuelve y desengancha todas las entidades o seres que están actualmente ancladas a mi o acechándome. Disuelve y borra de mi registro todos los acuerdos o contratos que he hecho con esas entidades. Devuélvelas a sus respectivos planos astrales. Limpia y elimina todos los programas negativos y energías discordantes asociadas con esas entidades.</p><p><b>Disuelve este bloqueo en todos los planos, dimensiones y líneas temporales en que se encuentre manifestado; desde el punto y momento espacio temporal en el cual se originó hasta este momento.</b></p><p><b>Gracias, gracias, gracias.</b></p>",
