import React, { Component, Fragment } from "react";
import ReactHtmlParser from "react-html-parser";
import store from "../store";
import TablaEntrenamiento from "./TablaEntrenamiento";
import Bloqueos from "./Bloqueos";

export default class Akasha extends Component {
  state = store;

  cambiarBloqueo = e => {
    let id = e.target.value,
      arrayActual = this.state.bloqueos.filter(
        bloqueo => bloqueo.id === parseInt(id)
      ),
      actual = arrayActual[0];

    this.setState({ actual });
  };

  render() {
    return (
      <Fragment>
        <ul className="collapsible">
         {/*  <li>
            <div className="collapsible-header">
              <i className="material-icons text-main">view_comfy</i>
              <b>Tabla de Entrenamiento</b>
            </div>
            <div className="collapsible-body">
              <TablaEntrenamiento valores={this.state.entrenamiento} />
            </div>
          </li> */}
          <li>
            <div className="collapsible-header">
              <i className="material-icons text-main">flash_off</i>
              <b>Bloqueos</b>
            </div>
            <div className="collapsible-body">
              <Bloqueos
                valores={this.state.bloqueos}
                cambiarBloqueo={this.cambiarBloqueo}
              />
            </div>
          </li>
          <li>
            <div className="collapsible-header">
              <i className="material-icons text-main">spa</i>Significado
            </div>
            <div className="collapsible-body">
              <article>
                {ReactHtmlParser(this.state.actual.significado)}
              </article>
            </div>
          </li>
          <li>
            <div className="collapsible-header">
              <i className="material-icons text-main">import_contacts</i>
              Preguntas
            </div>
            <div className="collapsible-body">
              <article>{ReactHtmlParser(this.state.actual.preguntas)}</article>
            </div>
          </li>
          <li>
            <div className="collapsible-header">
              <i className="material-icons text-main">flash_on</i>
              Desbloqueos
            </div>
            <div className="collapsible-body">
              {ReactHtmlParser(this.state.actual.desbloqueo)}
            </div>
          </li>
        </ul>
      </Fragment>
    );
  }
}
