import React from "react";

export default function Bloqueos(props) {
  return (
    <select name="bloqueo" onChange={props.cambiarBloqueo}>
      <option value="">Elige un bloqueo</option>
      {props.valores.map(el => (
        <option key={el.id} value={el.id}>
          {el.nombre}
        </option>
      ))}
    </select>
  );
}
